	export default {
		data() {
			return {
        goTopStatus: false,
        // 存储用户登录的凭证
        userLogin: ''
			}
    },
    onLoad() {
      this.userLogin = uni.getStorageSync('userLogin')
      // 设置导航条标题
      // uni.setNavigationBarTitle({
      //   title: ''
      // })
    },
		methods: {
			//监听页面高度(上滑或者下滑)
			onPageScroll(obj) {
				if (obj.scrollTop > 363) {
					return this.goTopStatus = true
				}
				this.goTopStatus = false
			},
			//点击回到顶部
			goToTop() {
				uni.pageScrollTo({
					scrollTop: 0
				})
      },
      // 去确定订单页面
      goConfirmOrder() {
        if (this.userLogin !== '') {
          uni.navigateTo({
            url: '/pages/loginRegister/loginRegister'
          })
        } else {
          uni.navigateTo({
            url: '/pages/confirmOrder/confirmOrder'
          })
        }
      },
      shareClick() {
        uni.share({
          provider: "weixin",
          scene: "WXSceneSession",
          success: res => {
            console.log(res)
          }
        })
      }
		}
	}
